<template>
  
  <b-container fluid id="mainContainer">
      <post-code-search></post-code-search>
      <local-details class="box" :coordinateLocationName="this.$store.state.location"></local-details>
      <weather-details class="box box-stretch"></weather-details>
      <weather-forecast class="box"></weather-forecast>
  </b-container>

</template>

<script>

import PostCodeSearch from './PostCodeSearch.vue'
import LocalDetails from './LocalDetails.vue'
import WeatherDetails from './WeatherDetails.vue'
import WeatherForecast from './WeatherForecast.vue'

export default {
  name: 'HomePage',
  components: {
    PostCodeSearch,
    LocalDetails,
    WeatherDetails,
    WeatherForecast
  },

  data() {
    return {
    }
  },
  
  created: function () {
    this.getWeatherByCoordinates();
  },

  methods: {

    getWeatherByCoordinates(){

      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(this.coordinatesResult);
      } else {
        console.log("Geolocation is not supported by this browser.");
      }

    },

    coordinatesResult(position) {

      const lat = position.coords.latitude;
      const lon = position.coords.longitude;

      var url = "http://api.openweathermap.org/data/2.5/weather?lat=" + lat + "&lon=" + lon + "&appid=" + this.$store.state.appid;

      this.$store.dispatch('getData', url);

    },

    convertDate(_timestamp) {

      var date = new Date(_timestamp*1000);

      var hours = date.getHours();
      var minutes = "0" + date.getMinutes();
      var seconds = "0" + date.getSeconds();

      var formattedTime = hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);

      return formattedTime;

    }
  }
}
</script>

<style scoped>

#mainContainer {
  
  display: flex;
  flex-direction: column;
  height: 100%;
  
  margin: 0;
  padding: 0;

  background: #1e5799; /* Old browsers */
  background: -moz-linear-gradient(top,  #1e5799 0%, #7db9e8 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top,  #1e5799 0%,#7db9e8 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom,  #1e5799 0%,#7db9e8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1e5799', endColorstr='#7db9e8',GradientType=0 ); /* IE6-9 */
  
}

.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.box-stretch {
  flex: 1;
}


</style>
