<template>

  <b-container>
  
    <b-row no-gutters class="justify-content-center">

      <b-col cols="6" class="text-right pr-2">
        <p>wind speed</p>
        <p>wind direction</p>
        <p>sunrise</p>
        <p>sunset</p>
        
      </b-col>

      <b-col cols="6" class="text-center pl-2">
        <p>{{ this.windSpeed }} m/s</p>
        <p>{{ this.windDeg }}</p>
        <p>{{ this.sunrise }}</p>
        <p>{{ this.sunset }}</p>
      </b-col>

    </b-row>

  </b-container>

</template>

<script>

export default {
  
  name: 'weather-details',

  data() {
    return {
      pressure: "", 
      humidity: "",
      windSpeed: "",
      windDeg: "",
      sunrise: "",
      sunset: ""
    }
  },

  computed: {
    forecastData() {
      return this.$store.state.forecastData;
    },
  },

  methods: {
    displayForecatsDetails(){
      this.windSpeed = this.$store.state.windSpeed;
      this.windDeg = this.degToCompass(this.$store.state.windDeg);
      this.sunrise = this.timestampToTime(this.$store.state.sunrise);
      this.sunset = this.timestampToTime(this.$store.state.sunset);
    },

    degToCompass(num) {
      var val = Math.floor((num / 22.5) + 0.5);
      var arr = ["N", "N/NE", "NE", "E/NE", "E", "E/SE", "SE", "S/SE", "S", "S/SW", "SW", "W/SW", "W", "WNW", "NW", "N/NW"];
      return arr[(val % 16)];
    },

    timestampToTime(_timestamp){
      var date = new Date(_timestamp*1000);
      var hours = date.getHours();
      var minutes = "0" + date.getMinutes();

      return hours + ':' + minutes.substr(-2);
    }
  },

  watch: {
    forecastData() {
      this.displayForecatsDetails();
    }
  }
}

</script>

<style scoped>

p {
  font-size: 12px;
  line-height: 12px;
}

</style>
