<template>

  <b-container>

    <b-row class="pt-3 pb-2 bg-black">
      <b-col cols="12" class="text-center">
        <div class="location">{{ coordinateLocationName }}</div>
      </b-col>
    </b-row>
    <b-row class="pt-2 pb-3 bg-black">
      <b-col cols="12" class="text-center inline">
        <div class="time">{{ time }} {{ date }}</div>
      </b-col>
    </b-row>

    <b-row no-gutters class="icon-and-temperature justify-content-center">

      <b-col cols="4" class="center-align">
        <div class="icon">
          <img :src="getWeatherIcon(this.$store.state.weatherIcon)" /><!-- <img :src="getWeatherIcon()" /> -->
          <div class="fs20-lh30 text-center ">{{ weatherText }}</div>
        </div>
      </b-col>

      <b-col cols="4" class="center-align">
        <div>
          <div class="pl-3 fs60-lh60">{{ Math.round(this.$store.state.curTemperature) }}&#176;</div>
        </div>
      </b-col>

      <b-col cols="3" class="text-center center-align">
        <div>
          <div class="fs12-lh12">feels like {{ Math.round(this.$store.state.feelTemperature) }}&#176;</div>
          <div class="fs20-lh30 border-bottom">max {{ Math.round(this.$store.state.maxTemperature) }}&#176;</div>
          <div class="fs20-lh30">min {{ Math.round(this.$store.state.minTemperature) }}&#176;</div>
        </div>
      </b-col>
    </b-row>

  </b-container>

</template>

<script>

import main from '../mixins/main'

export default {
  name: 'LocalDetails',

  props: ['coordinateLocationName'],
  
  mixins: [main],

  data() {
    return {
      month: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      week: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
      date: "",
      time: "",
      weathericonURL: "",
      weatherText: ""
    }
  },

  created() {
    this.updateTime();
    setInterval(this.updateTime, 1000);
  },

  computed: {
    getWeatherDetails() {
      return this.$store.state.dataFromCoordinates;
    }
  },

  watch: {
    getWeatherDetails() {
      this.weathericonURL = "http://openweathermap.org/img/wn/" + this.$store.state.weatherIcon + "@2x.png";
      this.weatherText = this.$store.state.weather;
    }
  },

  methods: {

    updateTime() {
        var cd = new Date();
        this.time = this.addRemoveZero(cd.getHours(), 2) + ':' + this.addRemoveZero(cd.getMinutes(), 2); // + ':' + this.addRemoveZero(cd.getSeconds(), 2);
        this.date = this.week[cd.getDay()] + ' ' + this.month[cd.getMonth()] + ' ' + cd.getDate() + ' ' + cd.getFullYear();
    },

    addRemoveZero(num, digit) {
        var zero = '';
        for(var i = 0; i < digit; i++) {
            zero += '0';
        }
        return (zero + num).slice(-digit);
    }
  }
}
</script>

<style scoped>

b-button img {
  height: 40px;
  object-fit: cover;
}

.location {
  font-size: 40px;
  line-height:  20px;
}

.time {
  font-size: 12px;
  line-height:  14px;
}

.date {
  font-size: 10px;
  line-height:  20px;
}

.icon {
  margin: 0;
  padding: 0;
  width: 100%;
}

.icon-and-temperature {
  height: 175px;
}

.border-bottom {
  border-bottom: 1px white;
}

.fs60-lh60 {
  font-size: 100px;
  line-height:  100px;
}

.fs40-lh40 {
  font-size: 40px;
  line-height:  40px;
}

.fs20-lh20 {
  font-size: 20px;
  line-height:  20px;
}

.fs20-lh30 {
  font-size: 15px;
  line-height:  25px;
}

.fs12-lh12 {
  font-size: 12px;
  line-height:  30px;
}


</style>
