import Vue from "vue";
import Vuex from "vuex";

import axios from 'axios'

Vue.use(Vuex);

export default new Vuex.Store({

    state: {
      appid: "f257f3f03d10410e2c80b41ea515709d",
      dataFromCoordinates: "",
      country: "",
      forecastData: "",
      location: "",
	    weather: "",
      weatherIcon: "",
    	curTemperature: 0,
      feelTemperature: 0,
    	maxTemperature: 0,
    	minTemperature: 0,
    	windSpeed: 0,
    	sunrise: "",
    	sunset: ""
    },

    mutations: {

        getData(state, _url){

          axios
            .get(_url)
            .then(response => {
              state.dataFromCoordinates    = response.data;
              state.country                = response.data.sys.country.toLowerCase();
              state.location               = response.data.name;
              state.weather                = response.data.weather[0].description;
              state.weatherIcon            = response.data.weather[0].icon;
              state.curTemperature         = response.data.main.temp - 273.15;
              state.feelTemperature        = response.data.main.feels_like - 273.15;
              state.minTemperature         = response.data.main.temp_min - 273.15;
              state.maxTemperature         = response.data.main.temp_max - 273.15;
              state.windSpeed              = response.data.wind.speed;
              state.windDeg                = response.data.wind.deg;
              state.sunrise                = response.data.sys.sunrise;
              state.sunset                 = response.data.sys.sunset;
            })
            .catch(error => {
              console.log(error);
            })
        },

        getForecastData(state, _url){

          axios
            .get(_url)
            .then(response => {
                  for(var j=0; j<response.data.list.length; j++){
                    var dt = new Date(response.data.list[j].dt*1000);
                  }

                  state.forecastData = response.data.list;
            })
            .catch(error => {
              console.log(error);
            })
        },
    },

    actions: {
        getData(context, _url) {
          context.commit('getData', _url);
        },

        getForecastData(context, _url) {
          context.commit('getForecastData', _url);
        }
    }
});