<template>

  <b-container>

    <b-row>
      <b-col class="pt-2 pb-2 bg-black">
        <b-input-group>
          <b-form-input v-model="postCode"></b-form-input>
          <b-input-group-append>
            <b-button variant="primary" v-on:click="validatePostcode">
              <v-icon name="search"/>
            </b-button>
          </b-input-group-append>
        </b-input-group>
      </b-col>
    </b-row>

  </b-container>

</template>

<script>

import axios from 'axios'

export default {
  name: 'PostCodeSearch',
  data() {
    return {
      postCode: ""
    }
  },

  methods: {

    getPostcodeData() {
      var url = "http://api.openweathermap.org/data/2.5/weather?zip=" + this.postCode + "," + this.$store.state.country + "&appid=" + this.$store.state.appid;
      this.$store.dispatch('getForecastData', url);
    },

    validatePostcode() { 
      var postcodeRegEx = /[A-Z]{1,2}[0-9]{1,2} ?[0-9][A-Z]{2}/i;
      if(postcodeRegEx.test(this.postCode)){
        this.getPostcodeData();
      }
    }
  }
}

</script>

<style scoped>

b-button img {
  object-fit: cover;
}

.input-group-lg > .form-control, .input-group-lg > .input-group-addon, .input-group-lg > .input-group-btn > .btn {
  height: 49px;
}

.btn-primary, .btn-primary:hover, .btn-primary:active, .btn-primary:visited {
  border: none;
  background-color: #627a96 !important;
}

</style>
