// src/mixins/clickMixin.js
export default {
  methods: {

    getWeatherIcon(_iconId){

      var base = "/assets/icons/";
      var url;

	      switch(_iconId) {
	        case "01d":
	          url = base + "sun.svg";
	          break;
	        case "01n":
	          url = base + "moon.svg";
	          break;
	        case "02d":
	          url = base + "sun-cloud.svg";
	          break;
	        case "02n":
	          url = base + "moon-cloud.svg";
	          break;
	        case "03d":
	          url = base + "light-cloud.svg";
	          break;
	        case "03n":
	          url = base + "light-cloud.svg";
	          break;
	        case "04d":
	          url = base + "dark-cloud.svg";
	          break;
	        case "04n":
	          url = base + "dark-cloud.svg";
	          break;
	        case "09d":
	          url = base + "rain.svg";
	          break;
	        case "09n":
	          url = base + "rain.svg";
	          break;
	        case "10d":
	          url = base + "rain-sun.svg";
	          break;
	        case "10n":
	          url = base + "rain-moon.svg";
	          break;
	        case "11d":
	          url = base + "thunder.svg";
	          break;
	        case "11n":
	          url = base + "thunder.svg";
	          break;
	        case "13d":
	          url = base + "snow.svg";
	          break;
	        case "13n":
	          url = base + "snow.svg";
	          break;
	        case "50d":
	          url = base + "fog.svg";
	          break;
	        case "50n":
	          url = base + "fog.svg";
	          break;
	        default:
	          url = base + "sun.svg";
	      }
	      
	      return url;
	    }
   }
}