<template>

  <b-container>

    <b-row class="justify-content-center bg-black">
      <b-col class="text-center week-day" v-for="(day, index) in weekDays" v-on:click="displayDayForecast(index)">
        <p class="center-align">{{ weekDaysStr[index] }}<br/>{{ weekDates[index] }}</p>
      </b-col>
    </b-row>

    <b-row>
      <b-col cols="12" class="forecast-icon-container temperature-gradient "> 
        <div v-for="(forecast, index) in forecastArr" class="text-center forecast-icon" >
          <img class="icon pt-1 pb-1" :src="getWeatherIcon(forecastArr[index].weather[0].icon)" />
          <p>{{ getIconTimeOfDay(index) }}</p>
          <div class="center-align">
            <div class="dot">{{ getIconTemperature(index) }}</div>
          </div>
        </div>
      </b-col>
    </b-row>

  </b-container>

</template>

<script>

import main from '../mixins/main'

export default {
  
  name: 'weather-forecast',
  
  mixins: [main],

  data() {
    return {
      today: "",
      clickDay: 0,
      weekDays: [],
      weekDaysStr: [],
      weekDates: [],
      forecastArr: [],
      tempDateArr: []
    }
  },

  mounted() {
    this.getFullWeatherForecastData();
  },

  computed: {
    forecastData() {
      return this.$store.state.forecastData;
    },
  },

  methods: {

    getFullWeatherForecastData(){

      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(this.getForecastByCoordinates);
      } else {
        console.log("Geolocation is not supported by this browser.");
      }

    },

    getForecastByCoordinates(position) {

      const lat = position.coords.latitude;
      const lon = position.coords.longitude;

      var url = "http://api.openweathermap.org/data/2.5/forecast/?lat=" + lat + "&lon=" + lon + "&appid=" + this.$store.state.appid;

      this.$store.dispatch('getForecastData', url);
      
    },

    displayDayForecats(){
      this.setWeekDays();
      setTimeout(() => { this.displayDayForecast(0); }, 0);
    },

    setWeekDays() {

      var tempDayArr = [];

      // POPULATE WEEKDAY ARRAY
      for(var g=0; g<this.forecastData.length; g++){
        var dt = new Date(this.forecastData[g].dt*1000);
        tempDayArr.push(dt.getDay());
      }

      this.weekDays = tempDayArr.filter(function(elem, index, self) {
          return index === self.indexOf(elem);
      });

      for(var d=0; d<this.weekDays.length; d++){
        switch (this.weekDays[d]) {
          case 0:
            this.weekDaysStr.push("Sun");
            break;
          case 1:
            this.weekDaysStr.push("Mon");
            break;
          case 2:
            this.weekDaysStr.push("Tue");
            break;
          case 3:
            this.weekDaysStr.push("Wed");
            break;
          case 4:
            this.weekDaysStr.push("Thu");
            break;
          case 5:
            this.weekDaysStr.push("Fri");
            break;
          case 6:
            this.weekDaysStr.push("Sat");
        }
      }

      // POPULATE WEEKDATE ARRAY
      for(var g=0; g<this.forecastData.length; g++){
        var dt = new Date(this.forecastData[g].dt*1000);
        this.tempDateArr.push(dt.getDate());
      }

      this.weekDates = this.tempDateArr.filter(function(elem, index, self) {
          return index === self.indexOf(elem);
      });
    },

    displayDayForecast(_day) {

      this.forecastArr = [];

      $(".week-day").each(function(){
        $(this).css({"color" : "white", "background-color": "transparent"});
      });

      $(".week-day:eq(" + _day + ")").css({"color" : "black", "background-color" : "yellow"});

      for(var d=0; d<this.forecastData.length; d++){

        var dt = new Date(this.forecastData[d].dt*1000);
        var dtDay = dt.getDay();
        
        if(dtDay == this.weekDays[_day]){
          this.forecastArr.push(this.forecastData[d]);
        } 
      }
    },
    
    getIconTimeOfDay(_idx){
      console.log(this.forecastArr[_idx]);
      var date = new Date(this.forecastArr[_idx].dt*1000);
      var hours = date.getHours();
      var minutes = date.getMinutes();

      return hours + ":00";
    },

    getIconTemperature(_idx){
      return Math.round(this.forecastArr[_idx].main.temp - 273.15);
    }

  },
  updated: function () {
    this.$nextTick(function () {
      var btm = this;

      var step = (45/Math.max.apply(Math,this.tempDateArr));

      $(".dot").each(function(index){
        $(this).css("bottom", btm.getIconTemperature(index)*step)
      });
    })
  },

  watch: {
    forecastData() {
      this.displayDayForecats();
    }
  }
}

</script>

<style scoped>

.forecast-icon-container {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 150px;
  white-space: nowrap;
  overflow-x: scroll;
}

.forecast-icon {
  position: relative;
  display: inline-block;
  width: 100px;
  height: 100%;
}

.week-day {
  height: 50px;
}

.week-day p {
  font-size: 14px;
  line-height: 25px;
}

.icon {
  margin: 0;
  padding: 0;
  width: 50%;
}

.dot {
  position: absolute;
  bottom:0;
  width:20px;
  height:20px;
  border-radius:50%;
  font-size:9px;
  color:black;
  line-height:20px;
  text-align:center;
  background:white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.temperature-gradient {

  background: rgb(0,51,96);
  background: -moz-linear-gradient(180deg, rgba(0,51,96,1) 0%, rgba(43,155,255,0) 100%);
  background: -webkit-linear-gradient(180deg, rgba(0,51,96,1) 0%, rgba(43,155,255,0) 100%);
  background: linear-gradient(180deg, rgba(0,51,96,1) 0%, rgba(43,155,255,0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#003360",endColorstr="#2b9bff",GradientType=1);
}

</style>
